console.log( '=== uploading module ===' )

export default class Uploading {

    get dialog_id() {
        return 'plugin-sr-uploading';
    }

    get root() {
        return '#' + this.dialog_id;
    }

    get success_icon() {
        return `<svg id="successAnimation" class="animated" xmlns="http://www.w3.org/2000/svg" width="70" height="70" viewBox="0 0 70 70"><path id="successAnimationResult" fill="#D8D8D8" d="M35,60 C21.1928813,60 10,48.8071187 10,35 C10,21.1928813 21.1928813,10 35,10 C48.8071187,10 60,21.1928813 60,35 C60,48.8071187 48.8071187,60 35,60 Z M23.6332378,33.2260427 L22.3667622,34.7739573 L34.1433655,44.40936 L47.776114,27.6305926 L46.223886,26.3694074 L33.8566345,41.59064 L23.6332378,33.2260427 Z"></path><circle id="successAnimationCircle" cx="35" cy="35" r="24" stroke="#979797" stroke-width="2" stroke-linecap="round" fill="transparent"></circle><polyline id="successAnimationCheck" stroke="#979797" stroke-width="2" points="23 34 34 43 47 27" fill="transparent"></polyline></svg>`;
    }

    get waring_icon() {
        return `<div class="svg-box">
                    <svg class="circular yellow-stroke">
                        <circle class="path" cx="75" cy="75" r="50" fill="none" stroke-width="5" stroke-miterlimit="10"></circle>
                    </svg>
                    <svg class="alert-sign yellow-stroke">
                        <g transform="matrix(1,0,0,1,-615.516,-257.346)">
                            <g transform="matrix(0.56541,-0.56541,0.56541,0.56541,93.7153,495.69)">
                                <path class="line" d="M634.087,300.805L673.361,261.53" fill="none"></path>
                            </g>
                            <g transform="matrix(2.27612,-2.46519e-32,0,2.27612,-792.339,-404.147)">
                                <circle class="dot" cx="621.52" cy="316.126" r="1.318"></circle>
                            </g>
                        </g>
                    </svg>
                </div>`;
    }

    get faild_icon() {
        return `<div class="svg-box">
                    <svg class="circular red-stroke">
                        <circle class="path" cx="75" cy="75" r="50" fill="none" stroke-width="5" stroke-miterlimit="10"></circle>
                    </svg>
                    <svg class="cross red-stroke">
                        <g transform="matrix(0.79961,8.65821e-32,8.39584e-32,0.79961,-502.652,-204.518)">
                            <path class="first-line" d="M634.087,300.805L673.361,261.53" fill="none"></path>
                        </g>
                        <g transform="matrix(-1.28587e-16,-0.79961,0.79961,-1.28587e-16,-204.752,543.031)">
                            <path class="second-line" d="M634.087,300.805L673.361,261.53"></path>
                        </g>
                    </svg>
                </div>`;
    }

    get template() {
        return `<div class="uploading">
                    <div class="loader"></div>
                    <div class="status">${ this.init || `正在上传，请稍等...` }</div>
                </div>`;
    }

    constructor( $$ui, init ) {
        this.$$ui = $$ui;
        this.init = init;
        this.dialog = undefined;
        this.diglog_action = {};
    }

    Close() {
        const select = "mduikit-dialog-gp#" + this.dialog_id;
        $( select ).css({
            opacity: 0,
            transform: 'translateY(1000px)'
        });
        setTimeout( () => {
            this.$$ui.DestoryById( 'opt-cancel-' + this.dialog_id );
            $( select ).remove();
        }, 200);
    }

    Render() {
        this.dialog = this.$$ui.Dialog( 'plugin-sr-uploading', '', this.template, '', this.diglog_action );
        $( 'html' ).append( this.dialog );
        this.diglog_action.open( this.dialog_id );
        return this;
    }

    Quit() {
        const btn_cancel = this.$$ui.Button( 'opt-cancel-'  + this.dialog_id,  '关 闭', { color: 'rgb(33, 150, 243)', type: 'flat', onclick: () => this.Close(), mode: 'secondary', css: 'margin-right: 10px' });
        $( '.uploading' ).append( btn_cancel );

    }

    Complete( text, cb ) {
        $( this.root ).find( '.loader' ).removeAttr( 'class' ).html( this.success_icon );
        $( this.root ).find( '.status' ).html( `<div>${ text || '👋 已调用成功。' }</div>` );
        setTimeout(() => {
            this.Close();
            cb && cb();
        }, 1500 );
    }

    Warn( text ) {
        $( this.root ).find( '.loader' ).removeAttr( 'class' ).html( this.waring_icon );
        $( this.root ).find( '.status' ).html( text || '嗨，上传时遇到了点问题，请稍后再试。' );
        this.Quit();
    }

    Faild( text ) {
        $( this.root ).find( '.loader' ).removeAttr( 'class' ).html( this.faild_icon );
        $( this.root ).find( '.status' ).html( text || '嗨，上传时遇到了点问题，请稍后再试。' );
        this.Quit();
    }
}