class Request {

    get endpoint() {
        return 'https://isslog-server-production.up.railway.app';
    }

    async fetcher( method, params ) {
        return await fetch( this.endpoint + method, params );
    }

    async upload( file ) {
        const formData = new FormData();
        formData.append( 'image', file );
        try {
            const response = await this.fetcher( '/api/upload', { method: 'POST', body: formData });
            return await response.json();
        } catch ( error ) {
            return new Promise(( resolve, reject ) => reject( error ));
        }
    }

    async memo( id, content ) {
        try {
            const response = await this.fetcher( '/api/memo', { method: 'POST', headers: { 'Content-Type': 'application/json' }, body: JSON.stringify({ id, content })});
            return await response.json();
        } catch ( error ) {
            return new Promise(( resolve, reject ) => reject( error ));
        }
    }

}

class Write {

    get root() {
        return '#editor';
    }

    get $root() {
        return $( this.root );
    }

    get $toolbar() {
        return $( '.toolbar' );
    }

    constructor() {
        this.editor = undefined;
        this.uploading = undefined;
        this.$root.val( tmpl );
        this.render();
    }

    getDoc() {
        return this.editor.getDoc();
    }

    getCurrent() {
        const doc    = this.getDoc(),
              cursor = doc.getCursor();
        return { doc, cursor };
    }

    render() {
        this.codeMirrorRender()
            .codeMirrorListen()
            .toolbarRender()
            .toolbarListen()
            .loadingRender( 'remove' );
        swtheme.render();
    }

    loadingRender( state ) {
        if ( state == 'add' ) {
            $( 'html' ).addClass( 'loading' );
        } else $( 'html' ).removeClass( 'loading' );
    }

    uploadingRender( label, type = 'init', callback ) {
        if ( type == 'init' ) {
            this.uploading = new Uploading( $$ui, label );
            this.uploading.Render();
        } else if ( type == 'complete' ) {
            this.uploading.Complete( label, callback );
        } else if ( type == 'faild' ) {
            this.uploading.Faild( label );
        }
    }

    codeMirrorRender() {
        this.editor = CodeMirror.fromTextArea( $( this.root )[0], {
            lineNumbers: false,
            theme: 'neo',
            mode: 'markdown',
        });
        $( '.CodeMirror' ).addClass( 'light' );
        return this;
    }

    codeMirrorListen() {

        this.editor.on( 'beforeChange', ( cm, change ) => {
            const doc    = this.getDoc();
            this.prevStr = doc.getLine( change.from.line );
        });

        this.editor.on( 'keyup', ( cm, event ) => {
            if ( event.keyCode === 13 ) {
                console.log( 'Enter key released', this.prevStr );
                const reg = /^(-|\*|\d+.) /i;
                if ( reg.test( this.prevStr )) {
                    let flag = this.prevStr.match( reg )[0];
                    if ( /\d/.test( parseInt( flag )) ) {
                        flag = ( parseInt( flag ) + 1 ) + '. ';
                    }
                    const { doc, cursor } = this.getCurrent();
                    doc.replaceRange( flag, cursor );
                }
            }
        });

        this.editor.on( 'change', ( cm, change ) => {
            const text = change.text.join( '' );
            if ( [ '[', '(' ].includes( text )) {
                const { doc, cursor } = this.getCurrent();
                let value = text == '[' ? ']' : text == '(' ? ')' : '';
                doc.replaceRange( value, cursor )
                doc.setCursor( cursor.line, cursor.ch );
            }
        });

        return this;
    }

    toolbarRender() {
        const tmpl = `<a class="upload">
                        <svg viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" width="18" height="18"><path d="M256 298.666667a85.333333 85.333333 0 1 1 85.333333 85.333333 85.333333 85.333333 0 0 1-85.333333-85.333333z m674.133333 415.146666l-256-341.333333a32.426667 32.426667 0 0 0-51.2 0l-170.666666 220.586667a21.333333 21.333333 0 0 1-33.28 0l-93.44-113.493334a32 32 0 0 0-49.92 0l-181.333334 234.24a42.666667 42.666667 0 0 0-8.96 26.026667v38.826667a32.426667 32.426667 0 0 0 32 32h789.333334a32.426667 32.426667 0 0 0 32-32v-39.253334a42.666667 42.666667 0 0 0-8.533334-25.6z" p-id="5528" fill="#424242"></path></svg>
                      </a>
                      <a class="preview">
                        <svg viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" width="18" height="18"><path d="M955.733333 497.066667c-81.066667-164.266667-249.6-277.333333-443.733333-277.333334s-362.666667 113.066667-443.733333 277.333334c-4.266667 10.666667-4.266667 21.333333 0 32 81.066667 164.266667 249.6 277.333333 443.733333 277.333333s362.666667-113.066667 443.733333-277.333333c4.266667-10.666667 4.266667-21.333333 0-32zM512 701.866667c-104.533333 0-189.866667-83.2-189.866667-189.866667s83.2-189.866667 189.866667-189.866667 189.866667 83.2 189.866667 189.866667-85.333333 189.866667-189.866667 189.866667z m0-309.333334c-66.133333 0-119.466667 53.333333-119.466667 119.466667s53.333333 119.466667 119.466667 119.466667 119.466667-53.333333 119.466667-119.466667-53.333333-119.466667-119.466667-119.466667z" p-id="4273" fill="#424242"></path></svg>
                      </a>
                      <a class="submit">
                        <svg viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" width="18" height="18"><path d="M796.2 466.4c0-2.4 0.4-4.8 0.4-7.2 0-130-103.6-235.2-231.4-235.2-92.2 0-171.4 54.8-208.6 134-16.2-8.2-34.4-13-53.6-13-59 0-108.2 43.8-117.6 101C114.6 470.4 64 538.2 64 618c0 100.4 80.2 182 179 182L448 800l0-160-96.4 0 160.4-167.4 160.4 167.2-96.4 0 0 160 220.6 0c90.4 0 163.4-75 163.4-166.8C960 541.2 886.6 466.6 796.2 466.4z" p-id="2361" fill="#424242"></path></svg>
                      </a>`;
        this.$toolbar.html( tmpl );

        return this;
    }

    toolbarListen() {
        $( '.upload' ).on( 'click', event => {
            const input  = document.createElement( 'input' ),
                  $input = $( input );
            $input.attr({ type : 'file' })
                .one( 'change', async event => {
                    const file = event.target.files[0];
                    if ( file ) {
                        this.uploadingRender( '图片上传中，请稍等...' );
                        const request = new Request(),
                              result  = await request.upload( file );
                        if ( result.code == 201 ) {
                            const { doc, cursor } = this.getCurrent(),
                                  image  = `![](${ result.result.url })`;
                            doc.replaceRange( image, cursor );
                            doc.setCursor( cursor.line, cursor.ch + image.length );
                            this.editor.focus();
                            this.uploadingRender( '👋 图片已上传成功。', 'complete' );
                        } else this.uploadingRender( '🥺 图片上传失败，请稍后再试。', 'faild' );
                    }
                });
            $input.trigger( 'click' );
        });
    
        $( '.preview' ).on( 'click', event => {
            const doc    = this.getDoc(),
                  conver = new showdown.Converter(),
                  html   = conver.makeHtml( doc.getValue() );
            this.previewRender( html );
        });
    
        $( '.submit' ).on( 'click', async event => {
            this.uploadingRender( '正在提交，请稍等...' );
            const doc     = this.getDoc(),
                  request = new Request(),
                  result  = await request.memo( 34, doc.getValue() );
            console.log( 'memo submit: ', result )
            result.code == 201
                ? this.uploadingRender( '✅ 已提交成功。', 'complete' )
                : this.uploadingRender( '⛔️ 提交失败，请稍后再试。', 'faild' );
        });

        return this;
    }

    previewRender( content ) {
        const tmpl = `<div id="preview" class="markdown-body">
                        ${ content }
                        <div class="close">
                            <svg viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" width="22" height="22"><path d="M512 992A480.512 480.512 0 0 1 32 512 480.512 480.512 0 0 1 512 32a480.512 480.512 0 0 1 480 480 480.512 480.512 0 0 1-480 480z m0-434.624l135.68 135.68a32 32 0 0 0 22.72 9.344 32 32 0 0 0 22.528-9.344 32 32 0 0 0 9.344-22.592 32 32 0 0 0-9.344-22.656L557.184 511.936l135.744-135.68a32 32 0 0 0 9.472-22.784 31.488 31.488 0 0 0-9.472-22.464 31.552 31.552 0 0 0-22.656-9.472 31.552 31.552 0 0 0-22.656 9.472L511.936 466.88 376.064 331.008a31.552 31.552 0 0 0-22.656-9.472 31.488 31.488 0 0 0-22.592 9.472 32 32 0 0 0 0 45.248l135.872 135.68-135.872 135.872a31.168 31.168 0 0 0-9.344 22.464 31.616 31.616 0 0 0 9.344 22.72 32 32 0 0 0 22.656 9.344A32 32 0 0 0 376 692.992l135.872-135.68z" fill="#333333"></path></svg>
                        </div>
                      </div>`;
        $( 'body' ).append( tmpl );
        const $target = $( '#preview' );
        $target.find( '.close' ).on( 'click', event => {
            $target.addClass( 'hide' ).on( "animationend", event => {
                $target.remove();
            });
        });
    }
}

$( document ).ready(() => {
    new Write();
});